import WS from 'ws'
import getGlobal from 'wsemi/src/getGlobal.mjs'
import getEnv from 'wsemi/src/getEnv.mjs'
import isfun from 'wsemi/src/isfun.mjs'


/**
 * WebSocket(Node.jsBrowser)
 *
 * @param {Object} opt 
 * @param {String} [opt.url='ws://localhost:8080'] WebSocketws'ws://localhost:8080'
 * @param {String} [opt.token='*'] token'*'
 * @param {Function} opt.open open
 * @param {Function} opt.close close
 * @param {Function} opt.message message
 * @param {Function} opt.error error
 * @returns {Promise} PromiseresolveWebsocketreject
 * @example
 *
 * import WSC from 'w-websocket-client/dist/w-websocket-client.umd.js'
 *
 * //opt
 * let opt = {
 *     url: 'ws://localhost:8080',
 *     token: '*',
 *     open: function() {
 *         console.log('client: open')
 *     },
 *     close: function() {
 *         console.log('client: close')
 *     },
 *     message: function(data) {
 *         console.log('client: message', data)
 *     },
 *     error: function(err) {
 *         console.log('client: error:', err)
 *     },
 * }
 *
 * //wsc
 * let wsc = null
 * new WSC(opt)
 *     .then(function(w){
 *
 *         //save
 *         wsc = w
 *
 *         //send
 *         let data = 'abc'
 *         wsc.send(data)
 *
 *     })
 *
 *
 */
function WWebsocketClient(opt) {
    let wsc = null //WebSocket


    //default
    if (!opt.url) {
        opt.url = 'ws://localhost:8080'
    }
    if (!opt.token) {
        opt.token = '*'
    }


    //isBrowser
    let isBrowser = getEnv().isBrowser


    //MixWS
    let MixWS
    if (isBrowser) {
        //browser, window, workerwindow, getGlobal
        MixWS = getGlobal().WebSocket //use browser websocket
    }
    else {
        //browsernodejs, ws
        MixWS = WS //use nodejs ws
    }


    //WebSocket, token
    try {
        wsc = new MixWS(opt.url + '/' + '?token=' + opt.token)
    }
    catch (err) {
        //throw (new Error('can not new MixWS'))
        wsc = null
    }


    //check, ie11
    if (wsc === null) {
        return {
            error: 'can not new MixWS'
        }
    }


    //bind
    if (isBrowser) {
        wsc.onopen = fOpen
        wsc.onmessage = function(ev) {
            let message = ev.data //data
            fMessage(message)
        }
        wsc.onclose = fClose
        wsc.onerror = fError
    }
    else {
        wsc.on('open', fOpen)
        wsc.on('message', fMessage)
        wsc.on('close', fClose)
        wsc.on('error', fError)
    }


    //fOpen
    function fOpen() {
        if (isfun(opt.open)) {
            opt.open()
        }
    }


    //fClose
    function fClose() {
        if (isfun(opt.close)) {
            opt.close()
        }
    }


    //fMessage
    function fMessage(data) {
        if (isfun(opt.message)) {
            opt.message(data)
        }
    }


    //fError
    function fError(err) {
        if (isfun(opt.error)) {
            opt.error(err)
        }
        wsc.close()
    }


    return wsc
}


export default WWebsocketClient
