/**
 * 
 *
 * @returns {Object} onemitonemit
 */
function EventEmitter() {

    //handles
    let handles = {}

    //on
    function on(eventName, callback) {
        if (!handles[eventName]) {
            handles[eventName] = []
        }
        handles[eventName].push(callback)
    }

    //emit
    function emit(eventName, ...arg) {
        if (handles[eventName]) {
            for (let i = 0; i < handles[eventName].length; i++) {
                handles[eventName][i](...arg)
            }
        }
    }

    return {
        on,
        emit,
    }
}


/**
 * 
 *
 * @param {*} [opt={}] {}
 * @param {Number} [opt.takeNumLimit=0] 0
 * @returns {Object} onpushgetcbonmessagepushgetcbmessage
 */
function WQueue(opt = {}) {

    //default
    if (!opt.takeNumLimit) {
        opt.takeNumLimit = 0
    }

    //takeNum
    let takeNum = 0

    //qs
    let qs = []

    //ee
    let ee = new EventEmitter()

    //get, like iterator
    function get() {
        if (qs.length > 0) {

            //add takeNum
            takeNum += 1

            //take first
            let v = qs.splice(0, 1)[0]

            //r
            //let r = { value: v, done: false }
            let r = v

            return r
        }
        else {

            //r
            //let r = { value: undefined, done: true }
            let r = null

            return r
        }
    }
    ee.get = get

    //cb
    function cb() {

        //minu takeNum
        takeNum -= 1

        //emit
        if (qs.length > 0) {
            ee.emit('message', qs)
        }

    }
    ee.cb = cb

    //push
    function push(v) {

        //push
        qs.push(v)
        //console.log('push', qs)

        //emit
        if (opt.takeNumLimit <= 0 || takeNum < opt.takeNumLimit) {
            ee.emit('message', qs)
        }

    }
    ee.push = push

    return ee
}


export default WQueue
